<template>
<div class="hello">
    <van-nav-bar title="Vant - https://vant-contrib.gitee.io/vant/v3/#/zh-CN/home" left-text="" right-text="" left-arrow @click-left="onClickLeft" @click-right="onClickRight" />
    <el-card>
        <h3></h3>
        <p> confirm </p>
        <van-cell title="" :value="date" @click="show = true" class="chosedate"/>
        <van-calendar v-model:show="show" @confirm="onConfirm" />
    </el-card>
</div>
</template>

<script>
import {
    Toast
} from 'vant';
import {
    ref
} from 'vue';

export default {
    setup() {
        const date = ref('');
        const show = ref(false);

        const formatDate = (date) => `${date.getMonth() + 1}/${date.getDate()}`;
        const onConfirm = (value) => {
            show.value = false;
            date.value = formatDate(value);
        };

        return {
            date,
            show,
            onConfirm,
        };
    },
    name: 'HelloWorld',
    props: {
        msg: String
    },
    methods: {
        onClickLeft() {
            this.$router.push('/Note')
        },
        onClickRight() {
            Toast.loading({
                message: '...',
                forbidClick: true,
                loadingType: 'spinner',
            });
        }
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
.hello {
    text-align: left;
}
.chosedate{
    border: 1px solid black;
    border-radius: 5px;
}
</style>
