<template>
<div class="hello">
    <el-card>
        <h3>localStorage </h3>
        <div class="good_data">
            {{good}}
        </div>
    </el-card>
    <el-card>
        <div class="good_op">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-button type="primary" @click="save(good)"></el-button>
                </el-col>
                <el-col :span="8">
                    <el-button type="success" @click="get('good')"></el-button>
                </el-col>
                <el-col :span="8">
                    <el-button type="danger" @click="del('good')"></el-button>
                </el-col>
            </el-row>
        </div>
    </el-card>
    <el-card>
        <h3>vue JS</h3>
        <el-row :gutter="10">
            <el-col :span="5">
                <el-button type="primary" @click="newObj('','male',28)"></el-button>
            </el-col>
            <el-col :span="5">
                <el-button type="success" @click="getObj('cat')"></el-button>
            </el-col>
            <el-col :span="14">
                <el-button type="danger">null-,localStorage</el-button>
            </el-col>
        </el-row>
    </el-card>
    <el-card>
        <h3>vue.jsinput</h3>
        <h4>$.ref:</h4>
        <el-card>
            <div class="">
                <p class="up-title"></p>
                <textarea name="" id="" cols="60" rows="5" placeholder="" ref="upTitleArea"></textarea>
                <br><br>
                <el-button type="success" @click="submint"></el-button>
            </div>
        </el-card>

        <h4>v-model</h4>
        <el-card>
            <div class="">
                <p class="up-title"></p>
                <textarea name="" id="" cols="60" rows="5" placeholder="" v-model="upTitleArea_v_model"></textarea>
                <br><br>
                <el-button type="success" @click="submint_v_model"></el-button>
            </div>
        </el-card>
        <h4></h4>
        <el-card>
            <el-row type="flex" class="row-bg" justify="center">
                <el-col :span="12">
                    <div class="grid-content bg-purple-light">
                        <el-input v-model="account_input" placeholder=""></el-input>
                    </div>
                </el-col>
            </el-row>
            <br><br>
            <el-row type="flex" class="row-bg" justify="center">
                <el-col :span="12">
                    <div class="grid-content bg-purple-light">
                        <el-input v-model="pwd_input" placeholder="" show-password></el-input>
                    </div>
                </el-col>
            </el-row>
            <br><br>
            <el-row type="flex" class="row-bg" justify="center">
                <el-col :span="12">
                    <div class="grid-content bg-purple-light">
                        <el-button type="success" @click="login"></el-button>
                    </div>
                </el-col>
            </el-row>
        </el-card>
    </el-card>
</div>
</template>

<script>
export default {
    name: 'LocalStorage',
    props: {
        msg: String
    },
    data() {
        return {
            good: {
                "icon": "el-icon-phone",
                "PRDINDR": "",
                "PRDTYPE": "",
                "PRDTYPENO": "0001",
                "PRDREMAIN": "",
                "PRDNM": "",
                "PRDNO": "0001",
                "PRDPRC": "1.00"
            },
            upTitleAreaResult: null,
            upTitleArea_v_model: '',
            // :
            account_input: '',
            account_result: '',
            pwd_input: '',
            pwd_result: ''

        }
    },
    methods: {
        login() {
            this.account_result = this.account_input
            this.pwd_result = this.pwd_input
            var userinfo = {}
            userinfo['account'] = this.account_result
            userinfo['pwd'] = this.pwd_result
            var userinfoStr = JSON.stringify(userinfo)
            alert(':'+userinfoStr)
        },
        submint_v_model() {
            this.upTitleAreaResult = this.upTitleArea_v_model;
            alert('v-model:' + this.upTitleAreaResult)
        },
        submint() {
            this.upTitleAreaResult = this.$refs.upTitleArea.value;
            alert('$.ref:' + this.upTitleAreaResult)
        },
        newObj(name, sex, age) {
            var obj = {}
            obj['name'] = name
            obj['sex'] = sex
            obj['age'] = age
            // 
            var objStr = JSON.stringify(obj)
            alert('cat localStorage:' + objStr)
            localStorage.setItem('cat', objStr)
        },
        getObj(key) {
            var catStr = localStorage.getItem(key)
            alert('localStoragecat :' + catStr)
            var catObj = JSON.parse(catStr)
            alert('cat name is:' + catObj.name)
        },
        save(data) {
            var tarStr = JSON.stringify(data)
            console.log('---save' + tarStr)
            localStorage.setItem('good', tarStr)

            this.$alert(tarStr, '', {
                confirmButtonText: '',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${ action }`
                    });
                }
            });
        },
        get(key) {
            var goodStr = localStorage.getItem(key);
            if (goodStr === null) {
                var msg = 'key=' + key + ',;'

                this.$alert(msg, '', {
                    confirmButtonText: '',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }
                });
            } else {
                console.log('---get' + goodStr)

                this.$alert(goodStr, '', {
                    confirmButtonText: '',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }
                });
            }
        },
        del(key) {
            localStorage.removeItem(key)
            var msg = 'key=' + key + ',;'

            this.$alert(msg, '', {
                confirmButtonText: '',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${ action }`
                    });
                }
            });
        },
    },
}
</script>

